const availableLocales = []
const state = {
  availableLocales,
  currentComponent: 'training',
  currentTheme: 'theme0',
  themes: [
    {
      name: 'Indian Red',
      class: 'theme0'
    },
    {
      name: 'Red',
      class: 'theme1'
    },
    {
      name: 'Blue',
      class: 'theme2'
    },
    {
      name: 'Light Blue',
      class: 'theme3'
    },
    {
      name: 'Green',
      class: 'theme4'
    },
    {
      name: 'Light Green',
      class: 'theme5'
    },
    {
      name: 'Brown',
      class: 'theme6'
    }
  ],
  wordBook: {
    folders: [
      {
        name: 'Main',
        removable: false,
        id: 1
      },
      {
        name: 'Learned',
        removable: false,
        id: 2
      }
    ],
    words: [
      {
        en: 'stream',
        ru: 'поток',
        folder: 1
      },
      {
        en: 'integrate',
        ru: 'интегрировать',
        folder: 1
      },
      {
        en: 'apple',
        ru: 'яблоко',
        folder: 1
      }
    ]
  },
  newWord: {
    en: '',
    ru: '',
    folder: ''
  }
}
const getters = {
  getAvailableLocales: state => state.availableLocales,
  getCurrentComponent: state => state.currentComponent,
  getCurrentTheme: state => state.currentTheme,
  getThemes: state => state.themes,
  getWordBook: state => state.wordBook,
  getNewWord: state => state.newWord
}
const actions = {
  setCurrentComponent ({ commit }, component) {
    commit('set', { type: 'currentComponent', data: component })
  },
  setCurrentTheme ({ commit }, theme) {
    commit('set', { type: 'currentTheme', data: theme })
    window.localStorage.setItem('currentTheme', JSON.stringify(theme))
  },
  setWordBook ({ commit }, obj) {
    commit('set', { type: 'wordBook', data: obj })
    window.localStorage.setItem('wordBook', JSON.stringify(obj))
  },
  setNewWord ({ commit }, obj) {
    commit('set', { type: 'newWord', data: obj })
  }
}
const mutations = {
  set (state, { type, data }) {
    state[type] = data
  }
}
export const store = new Vuex.Store({
  state,
  actions,
  getters,
  mutations
})

Vue.use(vuexI18n.plugin, store)
Object.keys(locales).forEach(function (lang) {
  availableLocales.push(lang)
  Vue.i18n.add(lang, locales[lang])
})

export default store



